<template>
  <div class="nodes-list">
    <section class="card">
      <h3 class="sub-title">Node </h3>
      <div class="action-bar">
        <el-button v-if="!swarmInfo"
                   type="primary"
                   @click="createSwarm">
          swarm
        </el-button>
        <el-button v-if="swarmInfo"
                   type="primary"
                   @click="leaveSwarm"
                   :disabled="nodeSelection.length<1">
          swarm
        </el-button>
        <el-button v-if="swarmInfo"
                   type="primary"
                   @click="dialogVisible = true">
          swarm
        </el-button>
      </div>
      <el-dialog
        title="swarm"
        :visible.sync="dialogVisible"
        width="60%">
        <el-form ref="form" :model="joinFormInfo" label-width="180px">
          <el-form-item label="">
            <el-switch v-model="joinFormInfo.isManager"></el-switch>
          </el-form-item>
          <el-form-item label="IP">
            <el-autocomplete
              v-model="joinFormInfo.ip"
              :fetch-suggestions="querySearch"
              placeholder="Ip"
              @select="handleSelect"
            ></el-autocomplete>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="joinSwarm"> </el-button>
        </span>
      </el-dialog>
      <el-table
        :data="nodesList"
        @selection-change="handleSelectionChange"
        style="width: 100%">
        <el-table-column
          type="selection">
        </el-table-column>
        <el-table-column
          prop="name"
          label="Name"
          :show-overflow-tooltip="true">
        </el-table-column>
        <el-table-column
          prop="role"
          label="Role"
          width="160">
        </el-table-column>
        <el-table-column
          prop="nanoCPUs"
          label="Nano CPUs"
          width="200">
        </el-table-column>
        <el-table-column
          prop="engine"
          label="Engine"
          width="160">
        </el-table-column>
        <el-table-column
          prop="address"
          label="IP Address"
          width="180">
        </el-table-column>
        <el-table-column
          prop="status"
          label="Status"
          width="180">
        </el-table-column>
      </el-table>
    </section>
  </div>
</template>

<script>
import {fetchNodesList} from '@/api/node.js'
import {fetchSwarmInfo, createSwarm, leaveSwarm, joinSwarm} from '@/api/swarm.js'
import {fetchEndpointInfo, fetchEndpointList} from '@/api/endpoint.js'

export default {
  components: {},
  data () {
    return {
      nodesList: null,
      swarmInfo: null,
      nodeSelection: [],
      currentEndpoint: null,
      dialogVisible: false,
      joinFormInfo: {
        isManager: false,
        ip: ''
      },
      endpointList: []
    }
  },
  computed: {
    managerNodes () {
      return this.nodesList.filter((node) => {
        return node.role === 'manager'
      })
    }
  },
  async mounted () {
    let currentEndpoint = await fetchEndpointInfo()
    currentEndpoint.ip = currentEndpoint.dockerUrl.split(':')[0]
    this.currentEndpoint = currentEndpoint
    let endpointList = await fetchEndpointList()
    endpointList.forEach((endpoint) => {
      endpoint.ip = endpoint.dockerUrl.split(':')[0]
    })
    this.endpointList = endpointList
    this.fetchSwarmInfo()
  },
  methods: {
    async fetchSwarmInfo () {
      let swarmInfo = await fetchSwarmInfo()
      this.swarmInfo = swarmInfo
      if (!swarmInfo) {
        this.nodesList = []
        return
      }
      let data = await fetchNodesList()
      this.nodesList = data.map((node) => {
        if (node.Status.State === 'ready') {
          return {
            name: node.Description.Hostname,
            role: node.Spec.Role,
            nanoCPUs: node.Description.Resources.NanoCPUs,
            engine: node.Description.Engine.EngineVersion,
            address: node.Status.Addr,
            status: node.Status.State
          }
        }
      })
    },
    handleSelectionChange (val) {
      this.nodeSelection = val
    },
    async createSwarm () {
      const error = await createSwarm(this.currentEndpoint)
      if (!error) {
        this.$message.success('')
        this.fetchSwarmInfo()
      } else {
        this.$message.error(error)
      }
    },
    async leaveSwarm () {
      let errormsg = ''
      this.nodeSelection.forEach(async (node) => {
        const error = await leaveSwarm(node.address)
        if (error) {
          errormsg += error
        }
      })
      if (errormsg === '') {
        this.$message.success('')
        this.fetchSwarmInfo()
      } else {
        this.$message.error(errormsg)
      }
    },
    async joinSwarm () {
      const role = this.joinFormInfo.isManager ? 'Manager' : 'Worker'
      const JoinToken = this.swarmInfo.JoinTokens[role]
      console.log('joinSwarm', this.currentEndpoint.ip, JoinToken, this.managerNodes[0].address)
      const error = await joinSwarm(this.joinFormInfo.ip, JoinToken, this.managerNodes[0].address)
      if (!error) {
        this.$message.success('')
        this.fetchSwarmInfo()
        this.dialogVisible = false
      } else {
        this.$message.error(error)
      }
    },
    querySearch (queryString, cb) {
      var endpointList = queryString ? this.endpointList.filter((endpoint) => {
        return endpoint.ip.indexOf(queryString) === 0
      }) : this.endpointList
      let results = []
      endpointList.forEach((item) => {
        if (!results.includes(item.ip)) {
          results.push({value: item.ip})
        }
      })
      console.log('results', results)
      cb(results)
    },
    handleSelect (item) {
      console.log('click', item.value)
      this.joinFormInfo.ip = item.value
    }
  }
}
</script>

<style lang="scss">
  .nodes-list {
    .sub-title {
      margin-bottom: 20px;
    }
    .action-bar {
      margin-bottom: 12px;
    }
  }
</style>



